@font-face {
  font-family: "RobotoMono";
  src: url(/calculadora-app/static/media/RobotoMono-Thin.d1a7b45f.ttf);
}

* {
  font-family: "RobotoMono", monospace;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #3f5c70, #4c778c , #4c778c , #2d4c51);
  color: azure;
}

.app {
  display: flex;
  flex-direction: column;
  
}


.calculator {
    height: 400px;
    width: 250px;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-self: center;
}

.buttons {
    background-color: rgb(159, 159, 159);
    height: 320px;
    display: grid;
    grid-template-columns:
        1fr 1fr 1fr 1fr;
}

/* utilizando variaveis para o css */

:root {
    --bg-bt: #f0f0f0;
    --border-bt: solid 0.5px grey
}

.button {
    font-weight: bold;
    font-size: 1.2em;
    background-color: #f0f0f0;
    background-color: var(--bg-bt);
    border: solid 0.5px grey;
    border: var(--border-bt);
    border-radius: 5px;
}

.button:hover {
    background-color: rgb(186, 186, 186);
}

.button:active {
    background-color: rgb(74, 74, 74);
    color: aliceblue;
}

.double {
    grid-column: span 2;
}

.operation {
    background-color: #dbd3bd;;
}

.display {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    height: 80px;
    background-color: rgba(51, 51, 51, 0.248);
    color: aliceblue;
    font-size: 2em;
    border: solid 3px rgb(56, 56, 56);
}
footer {
    height:90px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.614);
    font-size: 10px;
    text-align: center;
    color: #CCC;
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    margin-top: 20px;
    position: fixed;
}
.icon {
    width: 25px;
}

#contact a {
    margin: 10px;
    text-decoration: none;
}
